import React, { useState } from 'react'
import AdminServices from 'services/api/AdminServices'
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import cssVar from 'views/mobileView/styles';
import { toast } from 'react-toastify';
import db from 'libs/db';
import CustomAccordion from 'views/mobileView/components/atoms/Accordion';
import { Chip } from '@mui/material';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import addToRecentlyViewed from 'views/mobileView/components/atoms/addToRecentlyViewed';
import DisplayRecentlyViewed from 'views/mobileView/components/atoms/DisplayRecentlyViewed';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import AdminServicesPY from 'services/api/AdminServicesPY';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';


function ETFDetail(props) {
    const FUNDCODE = props.match.params.FundId;

    const [fund, setFund] = useState({});
    const [trailingReturns, setTrailingReturns] = React.useState({});
    const [trackingError, setTrackingError] = React.useState({});
    const [isWatchlisted, setIsWatchlisted] = useState(false);
    const [user, setUser] = useState({})
    const [fundNotFound,setFundNotFound] = useState(false)

    const [loading, setLoading] = useState(true)
    React.useEffect(() => {
        loadData();
    }, [FUNDCODE])

    const loadData = async () => {
        try {
            db.get(db.model.user).then(async (p) => {
                setUser(p.userData)
                await getWatchlist(p.userData.id)
                await getETFByID().then(async (fund) => {
                    if(Object.keys(fund).length){
                        addToRecentlyViewed({
                            type: 1,
                            ID: FUNDCODE,
                            Name: fund.ETFInfo.ETFName,
                            XIRR: fund.ETFInfo.FiveYrReturn,
                            Category: fund.ETFInfo.AssetClass,
                            AMCName: fund.ETFInfo.AMCName,
                            MinInv: fund.NAV.Price
                        })
                        await getDetail();
                    } else {
                        console.log("hjasbdhn")
                        setFundNotFound(true)
                    }
                });
                setLoading(false)
            });
        } catch (error) {
            console.log(error)
        }
    }

    const getETFByID = async () => {
        setLoading(true)
        try {
            let res = await AdminServices.getETFInfoBySymbol(FUNDCODE).then(res => res)
            if (res.type) {
                const a = JSON.parse(res.data)
                let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                console.log("Get ETF by symbol API", aa)
                setFund(aa)
                return aa
            }
            else {
                setFund({})
                toast.error("Could not find fund")
                return {}
            }
        } catch (error) {
            toast.error(error)
        }
    }
    const getDetail = async () => {
        try {
            const res1 = await AdminServicesPY.getETFTrailingReturns(FUNDCODE).then((res) => res);
            setTrailingReturns(res1.data)


            const res2 = await AdminServicesPY.getETFTrackingError(FUNDCODE).then((res) => res);
            setTrackingError(res2.data)

        } catch (error) {
            console.log(error)
        }
    }
    const getWatchlist = async (id) => {
        try {
            await db.get(db.model.watchlist).then(async (p) => {
                if (p === null) {
                    let res = await AdminServices.getWatchlist(id).then(res => res);
                    if (res.type === true) {
                        await db.set(db.model.watchlist, JSON.stringify(res.data))
                        setIsWatchlisted(res.data.some(f => f.FundType === 1 && f.FundCode === FUNDCODE))
                    }
                } else {
                    setIsWatchlisted(p.some(f => f.FundType === 1 && f.FundCode === FUNDCODE))
                }
            });
        } catch (error) {
            toast.error(error);
        }
    }

    const updateWatchlist = async () => {
        const data = {
            "clientId": user.id,
            "fundType": 1,
            "fundCode": FUNDCODE,
            "isActive": isWatchlisted ? 0 : 1
        }
        try {
            let res = await AdminServices.updateWatchlist(data).then(res => res)
            if (res.type) {
                setIsWatchlisted(!isWatchlisted)
            }
        } catch (error) {
            console.log(error)
        }

    }

    const Summary = () => {
        return (
            <div style={{ background: cssVar.getPropertyValue('--darkGreen'), padding: "4%", display: "flex", gap: "4%" }}>
                <AMCIcon amcName={fund?.ETFInfo?.AMCName} height="42px" width="42px" />
                <div style={{ flex: 1 }}>
                    <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white'), minHeight: "44px" }}>{fund?.ETFInfo?.ETFName}</div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>5Y CAGR</div>
                            <div className="header1dark" style={{ color: cssVar.getPropertyValue("--primaryGreen") }}>{parseFloat(fund?.ETFInfo?.FiveYrReturn || 0).toFixed(2)} %</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Category</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{fund?.ETFInfo?.AssetClass}</div>
                        </div>
                    </div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>NAV</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{CurrencyFormat(fund?.NAV?.Price,'long',2)}</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Symbol</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{fund?.ETFInfo?.Symbol}</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    const TrailingReturns = () => {
        return (
            <>
                {Object.keys(trailingReturns).length !== 0 ?
                    <div style={{ textAlign: "right", padding: "4%" }}>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div style={{ flex: 2 }}></div>
                            <div className='smallTextLight' style={{ flex: 1 }}>6 M</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>1 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>3 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>5 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>10 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>max</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2dark' style={{ flex: 2, color: cssVar.getPropertyValue('--darkGreen'), textAlign: "left" }}>ETF</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["183D"][FUNDCODE]}{trailingReturns["183D"][FUNDCODE]!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["365D"][FUNDCODE]}{trailingReturns["365D"][FUNDCODE]!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["1095D"][FUNDCODE]}{trailingReturns["1095D"][FUNDCODE]!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["1825D"][FUNDCODE]}{trailingReturns["1825D"][FUNDCODE]!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["3650D"][FUNDCODE]}{trailingReturns["3650D"][FUNDCODE]!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["max"][FUNDCODE]}{trailingReturns["max"][FUNDCODE]!=='NA'?'%':''}</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2light' style={{ flex: 2, textAlign: "left",color:cssVar.getPropertyValue('--gold') }}>Benckmark</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["183D"].Benchmark}{trailingReturns["183D"].Benchmark!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["365D"].Benchmark}{trailingReturns["365D"].Benchmark!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["1095D"].Benchmark}{trailingReturns["1095D"].Benchmark!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["1825D"].Benchmark}{trailingReturns["1825D"].Benchmark!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["3650D"].Benchmark}{trailingReturns["3650D"].Benchmark!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trailingReturns["max"].Benchmark}{trailingReturns["max"].Benchmark!=='NA'?'%':''}</div>
                        </div>
                    </div>
                    : ''
                }
            </>
        )
    }

    const TrackingReturns = () => {
        return (
            <>
                {Object.keys(trackingError).length !== 0 ?
                    <div style={{ textAlign: "right", padding: "4%" }}>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div style={{ flex: 2 }}></div>
                            <div className='smallTextLight' style={{ flex: 1 }}>6 M</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>1 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>3 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>5 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>10 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>max</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2dark' style={{ flex: 2, color: cssVar.getPropertyValue('--darkGreen'), textAlign: "left" }}>ETF</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["183D"].TE}{trackingError["183D"].TE!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["365D"].TE}{trackingError["365D"].TE!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["1095D"].TE}{trackingError["1095D"].TE!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["1825D"].TE}{trackingError["1825D"].TE!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["3650D"].TE}{trackingError["3650D"].TE!=='NA'?'%':''}</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{trackingError["max"].TE}{trackingError["max"].TE!=='NA'?'%':''}</div>
                        </div>
                    </div>
                    : ''
                }
            </>
        )
    }
    const Overview = () => {
        return (
            <>
                <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Tracking</div>
                        <div className='bodyTextDark'>{fund?.ETFInfo?.BenchmarkName || '-'}</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Inception</div>
                        <div className='bodyTextDark'>{(new Date(fund?.ETFInfo?.InceptionDate)).toLocaleDateString()|| '-'}</div>
                    </div>
                </div>
                <div style={{ display: "flex", padding: "4%" }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Expense ration</div>
                        <div className='bodyTextDark'>{fund?.ETFInfo?.ExpenseRatio || '--'}%</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Sub category</div>
                        <div className='bodyTextDark'>{fund?.ETFInfo?.SubCategory || '--'}</div>
                    </div>
                </div>
            </>
        )
    }

    const TopHolding = () => {
        const [selection, setSelection] = useState(0)
        return (
            <>
                <div style={{ display: "flex", padding: "3% 4% 0 4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light'), alignItems: "end" }}>
                    <div style={{ flex: 2 }}>
                        <Chip
                            style={{ marginRight: "8%" }}
                            className={selection === 0 ? "specialTab selected" : "specialTab"}
                            label="Companies"
                            onClick={() => {
                                setSelection(0)
                            }}
                        />
                        <Chip
                            className={selection === 1 ? "specialTab selected" : "specialTab"}
                            label="Sectors"
                            onClick={() => {
                                setSelection(1)
                            }}
                        />
                    </div>
                    <div className='header2light' style={{ flex: 1, textAlign: "right",  paddingBottom:'2%' }}>
                        Allocation (%)
                    </div>
                </div>
                {
                    selection === 0 ?
                        <>
                            {
                                fund?.CompanyInfo?.length?
                                    fund?.CompanyInfo?.map((c, i) => {
                                        return <div key={i} style={{ display: "flex", padding: "3% 4%", alignItems: "center", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <div style={{ flex: 2 }}>
                                                <div className='bodyTextDark'>{c.DisplayName}</div>
                                            </div>
                                            <div style={{ flex: 1 }}>
                                                <div className='header1faint' style={{ textAlign: 'right' }}>{Number(c.AssetPer).toFixed(1)}%</div>
                                            </div>
                                        </div>
                                    })
                                    :
                                    <div className='bodyTextDark' style={{margin:'4%',textAlign:'center'}}>No data Available</div>
                            }
                        </>
                        :
                        <>
                            {
                                fund?.SectorInfo?.length?
                                    fund?.SectorInfo?.map((s, i) => {
                                        return <div key={i} style={{ display: "flex", padding: "3% 4%", alignItems: "center", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <div style={{ flex: 2 }}>
                                                <div className='bodyTextDark'>{s.IndustryName}</div>
                                            </div>
                                            <div style={{ flex: 1 }}>
                                                <div className='header1faint' style={{ textAlign: 'right' }}>{Number(s.AssetPer).toFixed(1)}%</div>
                                            </div>
                                        </div>
                                    })
                                    :
                                    <div className='bodyTextDark' style={{margin:'4%',textAlign:'center'}}>No data Available</div>
                        }
                        </>
                }
            </>
        )
    }
    
    const accordionArray = [
        { key: "1", title: "Overview", body: Overview() },
        { key: "2", title: "Trailing returns", body: TrailingReturns() },
        { key: "3", title: "Tracking errors", body: TrackingReturns() },
        { key: "4", title: "Top holdings in the fund", body: TopHolding() },
    ]
    const Footer = () => {
        return (
            <div className='footer'>
                <button className='solidButton'
                    onClick={() => props.history.push({
                        pathname: '/portfolio/confirm-invest',
                        state: {
                            fundData: { ...fund.ETFInfo, FundCode: FUNDCODE, MinSIPAmount: fund.NAV.Price },
                            type: 1
                        }
                    })}
                >
                    Buy this ETF
                </button>
            </div>
        )
    }

    return (
        <>
            <div className='mainContainer'>
                {
                    loading ?
                        <>
                            <MainHeader
                                header="Invest in low cost ETFs"
                                navigation={props.history}
                            />
                            <ActivityLoaderMintbox />
                        </>
                        :
                        <>
                            <MainHeader
                                header="Invest in low cost ETFs"
                                isWatchlisted={isWatchlisted}
                                onWatchlistIconClick={() => { updateWatchlist() }}
                                right={fundNotFound?'':"bookmark-share"}
                                navigation={props.history}
                            />
                            {
                                fundNotFound?
                                    <div className='bigTextDark text-center mt-5'>Fund data not available</div>
                                    :
                                    <>
                                        <div className="dataContainer px-0">
                                            <Summary />
                                            <PerformanceChart type={1} fundCode={FUNDCODE} />

                                            <CustomAccordion accordionArray={accordionArray} />
                                            <DisplayRecentlyViewed navigation={props.history} header="Recently viewed ETFs" type={1} currentFundId={FUNDCODE} />
                                        </div>
                                        <Footer />
                                    </>
                            } 
                        </>
                }
            </div>
        </>
    )
}

export default ETFDetail